import axios from 'axios'
import store from '../store'

const baseDomain = (process.env.NODE_ENV === 'production') ? 'https://api.bantaycovid.dost6.ph/' : 'http://127.0.0.1:8000'
//const baseDomain = 'https://api.bantaycovid.dost6.ph/'
const baseUrl = `${baseDomain}/api`

const loginUrl = baseUrl + '/auth/login'
const logoutUrl = baseUrl + '/auth/logout'

const covid19HomeUrl = baseUrl + '/covid19'
const patientsUrl = baseUrl + '/patients'
const patientsPagedUrl = patientsUrl + '/paged'

const labresultsUrl = baseUrl + '/lab_results'
const patientsLabresult = labresultsUrl + '/patient_id'

const hospitalizationUrl = baseUrl + '/pui_status'
const patientsHospitalizationUrl = hospitalizationUrl + '/patient_id'

function getApi(url) {
    return axios({
        url: url,
        method: 'GET'
    })
}

function httpApiAuthenticated(url, method, payload) {
    let token = store.getters.auth_token

    return axios({
        url: url,
        data: payload,
        headers: {
            Authorization: `Bearer ${token}`
        },
        method: method
    })
}

function postApiAuthenticated(url, payload) {
   return httpApiAuthenticated(url, 'POST', payload)
}

function putApiAuthenticated(url, payload) {
    return httpApiAuthenticated(url, 'PUT', payload)
}

export function loginApi(payload) {
    return axios({url: loginUrl, data: payload, method: 'POST'})
}

export function logoutApi() {
    let token = store.getters.auth_token

    return axios({
        url: logoutUrl,
        headers: {
            Authorization: `Bearer ${token}`
        },
        method: 'GET'
    })
}

export function getPUIMap() {
    return getApi(covid19HomeUrl)
}

export function getPatients() {
    return getApi(patientsUrl)
}

export function getPatientById(id) {
    return getApi(`${patientsUrl}/${id}`)
}

export function getPatientsPaged(page, itemsPerPage = 10) {
    const fetchUrl = `${patientsPagedUrl}/?page=${page}`
    return getApi(fetchUrl)
}

export function getPatientLabResults(patient_id) {
    return getApi(`${patientsLabresult}/${patient_id}`)
}

export function getPatientHospitalizations(patient_id) {
    return getApi(`${patientsHospitalizationUrl}/${patient_id}`)
}

export function addPatient(payload) {
    return postApiAuthenticated(patientsUrl, payload)
}

export function editPatient(patient_id, payload) {
    return putApiAuthenticated(`${patientsUrl}/${patient_id}`, payload)
}

export function addLabResult(payload) {
    return postApiAuthenticated(labresultsUrl, payload)
}

export function addHospitalization(payload) {
    return postApiAuthenticated(hospitalizationUrl, payload)
}


