<template>
    <v-app>
        <v-navigation-drawer
                app
                v-model="drawer"
                clipped-left
                dark
                disable-resize-watcher
                temporary
        >
            <v-list>
                <v-list-item v-for="item in menuSideItems" :key="item.title" :to="item.link" exact>
                    <v-list-item-action>
                        <v-icon>{{item.icon}}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>
                            {{ item.title }}
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>

        </v-navigation-drawer>
        <v-app-bar
                app
                clipped-left
                color="primary"
                dark>
            <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
            <v-toolbar-title class="pl-0 pr-4 mx-0">
                <router-link tag="span" style="cursor: pointer" :to="{name: 'home'}" exact>
                    Bantay COVID-19
                </router-link>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-menu
                    offset-y
            >
                <template v-slot:activator="{ on }">
                    <v-btn icon v-on="on">
                        <v-icon>mdi-dots-vertical</v-icon>
                    </v-btn>
                </template>
                <v-list dense>
                    <v-list-item
                            v-for="item in meMenuItems"
                            :key="item.name"
                            :to="item.link"
                    >
                        <v-list-item-icon>
                            <v-icon v-text="item.icon"></v-icon>
                        </v-list-item-icon>
                        <v-list-item-content class="ml-3">
                            <v-list-item-title>
                                {{item.name}}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list>
            </v-menu>
        </v-app-bar>
        <v-content>
            <v-container class="pa-0 ma-0" fill-height fluid >
                 <router-view></router-view>
            </v-container>
        </v-content>
        <v-footer v-if="$vuetify.breakpoint.mdAndUp" color="primary" app fixed clipped-left>
            <span class="primary--text text--lighten-5 ml-2">Copyright &copy; 2020 DOST VI</span>
        </v-footer>
    </v-app>
</template>

<script>
    import {mapGetters} from 'vuex'

    export default {
        name: 'App',
        created() {
         // alert('This site is under testing and development. Not for the public. Thank You!')
        },
        computed: {
            ...mapGetters({
                isauthenticated: 'isauthenticated',
                username: 'username'
            }),
            meMenuItems() {
                let menu = []
                if (this.isauthenticated) {
                    menu = menu.concat([
                        {name: this.username, icon: 'mdi-account-circle'},
                        {name: 'Logout', link: {name: 'logout'}, icon: 'mdi-exit-to-app'}
                    ])
                } else {
                    menu = menu.concat([
                        {name: 'Login', link: {name: 'login'}, icon: 'mdi-login'},
                    ])
                }

                // menu = menu.concat([
                //     {name: 'About Us', link: {name: 'about'}, icon: 'mdi-information'},
                // ])
                return menu
            }
        },
        data() {
            return {
                drawer: false,
                menuSideItems: [
                    {title: 'COVID-19 Home', link: {name: 'home'}, icon: 'mdi-home'},
                    {title: 'PUIs', link: {name: 'pui'}, icon: 'mdi-account-group'},
                ]
            }
        }
    }
</script>

<style>
    html,
    body,
    #app {
        width: 100%;
        height: 100%;
        overflow: visible !important;
    }
    .v-application--wrap {
        min-height: 100% !important;
    }
</style>
