<template>
    <v-row class="pa-0 ma-0 fill-height">
        <v-col v-if="api_loaded && !api_error" cols="12">
            <v-row>
                <v-col cols="12"
                       md="6"
                       id="map_container"
                       :class="{'px-8': $vuetify.breakpoint.smAndDown}"
                       :order="$vuetify.breakpoint.mdAndUp ? 1 : 2">
                    <app-map :response_data="response.data"
                             v-on:choropleth-set="onDashboardSet"
                             v-on:choropleth-clear="onDashboardClear"
                    ></app-map>
                </v-col>
                <v-col cols="12" md="6" :order="$vuetify.breakpoint.mdAndUp ? 2 : 1">
                    <v-card>
                        <app-case-dashboard :title="dashboard.title"
                                            :positive="dashboard.positive"
                                            :recovered="dashboard.recovered"
                                            :deaths="dashboard.deaths"
                                            :citymunCode="dashboard.citymunCode"
                                            :unresolved="dashboard.unresolved"
                                            :negative="dashboard.negative"
                                            v-on:case-dashboard-clear="onDashboardClear"
                                            v-on:case-dashboard-set="onDashboardSet"
                        ></app-case-dashboard>
                    </v-card>
                </v-col>
                <v-col cols="12" md="6" :order="3">
                    <v-card>
                        <app-pui-status :provinces_and_hucs_data="response.provinces_and_hucs"></app-pui-status>
                    </v-card>
                </v-col>
                <v-col cols="12" md="6" :order="3">
                    <v-card>
                        <app-covid-status :covid_case="response.provinces_and_hucs"></app-covid-status>
                    </v-card>
                </v-col>
            </v-row>
        </v-col>
        <v-overlay v-if="!api_loaded || api_error" :value="true" absolute>
            <v-icon v-if="api_error" size="64">mdi-alert</v-icon>
            <v-progress-circular v-else indeterminate size="64"></v-progress-circular>
        </v-overlay>
    </v-row>
</template>

<script>
    import Map from "../components/Homepage/Map"
    import CaseDashboard from "../components/Homepage/CaseDashboard"
    import PuiStatus from "../components/Homepage/PuiStatus"
    import CovidStatus from "../components/Homepage/CovidStatus"
    import {getPUIMap} from "../util/api"

    export default {
        name: "COVID19_Home",
        components: {
            'app-map': Map,
            'app-case-dashboard': CaseDashboard,
            'app-pui-status': PuiStatus,
            'app-covid-status': CovidStatus
        },
        created() {
            getPUIMap().then(res => {
                const {data} = res

                console.log(data)

                this.response.data = data.data
                this.response.total = data.total
                this.response.provinces_and_hucs = data.provinces_and_hucs
                this.resetDashboardToWV()


                this.api_loaded = true
                this.api_error = false
            }).catch(error => {
                this.api_loaded = true
                this.api_error = true
            })
        },
        data() {
            return {
                api_loaded: false,
                api_error: false,
                response: {
                    data: [],
                    total: {},
                    provinces_and_hucs: []
                },
                dashboard: {
                    title: 'Western Visayas',
                    positive: 0,
                    recovered: 0,
                    deaths: 0,
                    unresolved: 0,
                    negative: 0,
                    citymunCode: "000000"
                }
            }
        },
        methods: {
            onDashboardClear() {
                this.resetDashboardToWV()
            },
            onDashboardSet(citymunCode) {
                console.log(citymunCode)
                const item = this.response.data.find( x => {
                    return x['citymunCode'] === citymunCode
                })

                if (!item) {
                    this.resetDashboardToWV()
                }

                this.dashboard = {
                    title: item.citymun,
                    positive: item.covid_positive,
                    recovered: item.covid_recovered,
                    deaths: item.covid_deaths,
                    unresolved: item.pui_unresolved,
                    negative: item.pui_negative,
                    citymunCode: citymunCode
                }
            },
            resetDashboardToWV() {
                this.dashboard = {
                    title: 'Western Visayas',
                    positive: this.response.total.covid_positive,
                    recovered: this.response.total.covid_recovered,
                    deaths: this.response.total.covid_deaths,
                    unresolved: this.response.total.pui_unresolved,
                    negative: this.response.total.pui_negative,
                    citymunCode: "000000"
                }
            }
        }
    }
</script>

<style scoped>
    #map_container {
        width: 100%;
        height: calc(100vh - 180px);
    }

    #bottom_ui_container {
        width: 100%;
    }
</style>