import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import {loginApi, logoutApi} from "../util/api";

Vue.use(Vuex)

const mutationsBlacklist = []

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
  reducer: (state) => ({
    auth: state.auth,
  }),
  filter: (mutation) => mutationsBlacklist.indexOf(mutation.type) === -1
})

export default new Vuex.Store({
  state: {
    auth: {
      user: {
        name: '',
        email: '',
      },
      token: '',
      // roles: [],
      // permissions: []
    },
  },
  mutations: {
    setAuth(state, payload) {
      state.auth.user.name = payload.name
      state.auth.user.email = payload.email
      state.auth.token = payload.token
      // state.auth.roles = payload.roles
      // state.auth.permissions = payload.permissions
    },
    clearAuth(state) {
      console.log('cleara auth')
      state.auth.user.name = ''
      state.auth.user.email = ''
      state.auth.token = ''
      // state.auth.roles = []
      // state.auth.permissions = []
    },
  },
  actions: {
    login({commit}, payload) {
      return new Promise((resolve, reject) => {
        loginApi(payload)
            .then(resp => {
              let data = {
                name: resp.data.user.name,
                email: resp.data.user.email,
                token: resp.data.access_token,
                // roles: resp.data.roles,
                // permissions: resp.data.permissions,
              }
              commit('setAuth', data)
              resolve()
            })
            .catch(error => {
              if (error.response) {
                if (error.response.status === 401) {
                  reject('Invalid email/password entered.') //401 error receive
                } else {
                  reject('Unknown error in authenticating account.') //
                }

              } else if (error.request) {
                reject('Cannot connect to server. Please try again.') //
              } else {
                reject('Application error. Please restart App.') //
              }
              reject('')
            })
      })
    },
    logout({commit}) {
        return logoutApi().then(response => {
          commit('clearAuth')
        }).catch(error => {
          commit('clearAuth')
        })
    },
  },
  getters: {
    username: state => (state.auth.user.name != null && state.auth.user.name != '') ? state.auth.user.name : 'GUEST',
    // userRoles: state => state.auth.roles,
    // userPermissions: state => state.auth.permissions,
    auth_token: state => state.auth.token,
    isauthenticated: state => ((state.auth.token !== '') && (state.auth.token !== null)),
  },
  modules: {
  },
  plugins: [vuexLocal.plugin]
})
